import { Injectable } from "@angular/core";
import { MatPaginatorIntl } from "@angular/material";

@Injectable()
export class MatPaginatorIntlGerman extends MatPaginatorIntl {
    itemsPerPageLabel = 'Itens por pgina: ';
    nextPageLabel = 'Pgina seguinte';
    previousPageLabel = 'Pgina anterior';
    firstPageLabel = 'Primeira pgina';
    lastPageLabel = 'ltima pgina';

    getRangeLabel = (page: number, pageSize: number, length: number) => {
        return ((page * pageSize) + 1) + ' - ' + ((page * pageSize) + pageSize) + ' de ' + length;
    }
}